import React from 'react';
import SidebarItem from './SidebarItem';
const Sidebar = ()=>{
    return (
        <nav>
            <ul>
                <SidebarItem item="Home" toLink='/' />
                <SidebarItem item="Education" toLink='/education' />
                <SidebarItem item ="Skills" toLink='/skills' />
                <SidebarItem item="Projects" toLink='/projects'/>
            </ul>
        </nav>
    )
};

export default Sidebar;