import React from 'react';
import Card from 'react-bootstrap/Card';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCalendar} from '@fortawesome/free-regular-svg-icons';
import {faMapMarkerAlt} from '@fortawesome/free-solid-svg-icons'
import Col from 'react-bootstrap/Col';
const School = (props) =>{
    return (
        <Card className="school-card mt-5">
            <Card.Header className="degree mb-2">{props.degree}</Card.Header>
            <Card.Body className="p-2">
                <Card.Title className="school">{props.schoolName}</Card.Title>
                <Card.Text className="d-flex mb-2">
                    <Col className ="d-flex align-items-center">
                        <FontAwesomeIcon icon={faCalendar}/>
                        <div className ="ml-2">
                            {props.startDate} - {props.endDate}
                        </div>
                    </Col>
                    <Col className ="d-flex align-items-center">
                        <FontAwesomeIcon icon={faMapMarkerAlt}/>
                        <div className ="ml-2">
                            {props.location}
                        </div>
                    </Col>
                </Card.Text>
                <Card.Text className="px-2">GPA: {props.gpa}</Card.Text>

            </Card.Body>
        </Card>
    )
};

export default School;
