import React from 'react';
// import profilepic from "/portrait.jpg";

import ReactTypingEffect from 'react-typing-effect';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faGithub} from '@fortawesome/free-brands-svg-icons';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
const Home = () =>{
    const profilepic = process.env.PUBLIC_URL + "/portrait.jpg";
    return(
        // <div className="d-flex flex-column align-items-center">
        <Container className="p-0  d-flex flex-column h-100">
            <Row className="justify-content-center mt-auto">
                <img src={profilepic} className="profilepic"/>
            </Row>
            <Row className="justify-content-center">
                <ReactTypingEffect 
                    text={["Hi, my name is Kha"]}
                    speed={100}
                    eraseDelay={2000}
                    eraseSpeed={200}
                    className="typing-effect mt-4"
                />
            </Row>
            <Row className="justify-content-center mt-auto mb-5">
                <a href="https://github.com/khanguyen74" className="icon-link"><FontAwesomeIcon icon={faGithub} style={{fontSize:"30px"}} /></a>
                
            </Row>
        {/* // </div> */}
        </Container>
    )
};

export default Home;