import React from 'react';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';

import School from './School';
const Education = () =>{
    return(
        <Container className="container-education">
            <Row className="d-flex">
                <School gpa="4.0/4.0" degree="B.S in Computer Science" 
                    schoolName="University of Texas at Dallas"
                    startDate="08/2019" endDate="05/2021"
                    location="Richardson, TX, USA"/>
            </Row>
            <Row>
                <School gpa="4.0/4.0"degree="A.S in Computer Science" 
                    schoolName="Richland College"
                    startDate="08/2016" endDate="05/2019"
                    location="Dallas, TX, USA"/>

            </Row>
        </Container>
    )
};

export default Education;